* {
  margin: 0;
  padding: 0;
  box-sizing: content-box;
}

body {
  max-width: 1024px;
  margin: 0 auto;
}
body #header {
  max-width: 1024px;
  display: flex;
  justify-content: flex-end;
  background-color: #ccc;
  margin: 0 auto;
  padding: 25px 0;
}
body #header .login_link {
  text-transform: uppercase;
  text-decoration: none;
  color: #363135;
  font-size: 0.8rem;
  font-weight: 700;
}
body #welcome {
  display: flex;
  justify-content: space-between;
  background-color: aliceblue;
}
body #welcome form {
  display: flex;
  flex-direction: column;
  width: 30%;
  gap: 5px;
  margin: 15px 0 0;
  padding: 12px 8px;
}
body #welcome form li {
  list-style: none;
  display: flex;
  justify-content: space-between;
}
body #welcome form #form_submit_btn {
  padding: 12px 8px;
  border-radius: 5px;
  border: 2px solid #ccc;
  outline: #0063aa;
  margin-top: 15px;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 1.2px;
  text-transform: uppercase;
}
body #text {
  display: flex;
  width: 50%;
  background-color: #383838;
  color: #d4d4d4;
  padding: 10px;
  line-height: 1.4;
  font-size: 1.2rem;
  border-radius: 5px 5px 0 0;
  margin-top: 10px;
  box-shadow: 0 -10px 10px #a6ffd3;
}